<template>
  <b-overlay :show="loading || !form">
    <div class="profile">
      <!-- form.type === 'corporate' -->
      <Broker v-if="form.type === 'corporate'" :form="form" />
      <Normal_User v-else :form="form" />
    </div>
  </b-overlay>
</template>

<script>
import Normal_User from '@/components/profile/Normal_User';
import Broker from '@/components/profile/Broker';

export default {
  components: {
    Normal_User,
    Broker,
  },
  layout: 'dashboard',
  data() {
    return {
      timeout: null,
      form: {},
      loading: false,
      serverErrors: null,
      successMessage: null,
      userType: ''
    };
  },
  computed:{
    appEnv(){
      return this.$config.APP_ENV
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr'
    },
  },
  head(){
    return {
      title: `${this.dir === "ltr" ? "Profile Page" : "الصفحة الشخصية"} ${this.appEnv !== 'production' ? '| Staging' : ''}`,
      meta: [
        // hid is used as unique identifier. Do not use `vmid` for it as it will not work
        {
          hid: 'description',
          name: 'description',
          content: this.dir === "ltr" ? "Profile Page" : "الصفحة الشخصية",
        }
      ]
    }
  },
  async created() {
    try {
      this.loading = true;

      const { Data } = await this.$axios.$get('/user/userDetails');
      
      this.form = Data;

      console.log(this.form);

      this.userType = Data.type;

      this.serverErrors = null;
      this.loading = false;
    } catch (error) {
      this.serverErrors = error.response.data.Data;
      this.loading = false;
    }
  },
  destroyed() {
    this.successMessage = null;
  },
  methods: {
    updateProfileHandler(prop, value) {
      // debouncing
      if (this.timeout) clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        this.form[prop] = value || '';
      }, 300);
    },
    async saveProfileHandler() {
      try {

        if(this.form.type === 'corporate' && this.userType !== 'corporate'){
          const res = await this.$axios.$post('/user/corprate/changeType');
          if(res.Status === 200){
            alert('Changed To Broker')
          }
        }

        this.loading = true;

        Object.entries(this.form).map((item) => {
          if (!item[1] || item[1] == 'undefined') {
            this.$delete(this.form, item[0]);
          }
        });

        let formObj = null;
        formObj = {
          ...this.form,
        };
      
        formObj.nationality = this.form.nationality?.replace(/ /g, '_');
        formObj[name] = this.form.full_name ? this.form.full_name : '';
        typeof formObj.avatar === 'string' ? delete formObj.avatar : null;

        const formData = new FormData();
        for (let key in formObj) {
          formData.append(key, this.form[key]);
        }

        const { Data } = await this.$axios.$post('/user/updateUser', formData);

        this.serverErrors = null;
        this.successMessage = Data.message;
        this.loading = false;

      } catch (error) {
        this.serverErrors = error.response.data;
        this.successMessage = null;
        this.loading = false;
      }
    },
  },
};
</script>

<style lang="scss">
  .profile {
    @media (min-width: 768px) and (max-width: 991px) {
      margin-top: 70px !important;
    }
  }
  svg {
      cursor: pointer;
  }
  svg path {
      transition: 0.5s;
  }

  svg:hover path {
      fill: #f00 !important;
  }
  .card-body {
      display: flex;
      justify-content: space-between;
      align-items: center;
  }
  .makeGap {
      gap: 15px;
  }
</style>