            {{ $t('profile.basicInformation') }}
          </h2>
          <b-row class="mt-4">
            <b-col cols="12" class="mb-4">
              <h4 class="font-weight-bold">Full Name</h4>
              <b-form-input class="bg-transparent" @input="updateProfile(`full_name`, $event)" v-model="form.full_name" placeholder="Enter your name"></b-form-input>
            </b-col>
            <b-col cols="12" class="mb-4">
              <h4 class="font-weight-bold">Gender</h4>
              <b-form-select v-model="form.gender" :options="genderOptions"></b-form-select>
            </b-col>
            <b-col cols="12" class="mb-4">
              <h4 class="font-weight-bold">Address</h4>
              <b-form-input class="bg-transparent" v-model="form.address" placeholder="Address"></b-form-input>
            </b-col>
          </b-row>
        </b-col>
        <hr class="w-90 mx-auto">
        <!-- Contact Information -->
        <b-col cols="12" sm="9" lg="6" offset-lg="0" class="mx-auto py-2">
          <h2 class="font-18 color-black font-weight-bold">
            {{ $t('profile.contactInformation') }}
          </h2>
          <b-row class="md-4">
            <b-col cols="12" class="mt-4">
              <h4 class="font-weight-bold">Email Address</h4>
              <b-form-input class="bg-transparent" @input="updateProfile(`full_name`, $event)" v-model="form.full_name" placeholder="Enter your name"></b-form-input>
            </b-col>
            <b-col cols="12" class="mt-4">
              <vue-tel-input
                v-model="form.mobile"
                @input="updateProfile(`mobile`, $event)"
                :label="$t('form.mobileNumber')"
                :validation-name="$t('form.mobileNumber')"
                validation-rules="required|phoneNumber"
                input-id="mobile-number"
                :placeholder="$t('form.mobileNumber')"
                mode="international"
                dropdownOptions.showSearchBox="true"
              ></vue-tel-input>
            </b-col>
          </b-row>
        </b-col>
        <hr class="w-90 mx-auto">
        <b-col cols="12" sm="9" lg="6" offset-lg="0" class="mx-auto pt-4">
          <h3 class="font-18 color-black font-weight-bold text-center">
            Forget Password
          </h3>
          <b-col cols="12" class="mt-4 px-0">
            <h4 class="font-weight-bold">Login Email</h4>
            <b-form-input class="bg-transparent" @input="updateProfile(`full_name`, $event)" v-model="form.email" placeholder="Enter your name"></b-form-input>
          </b-col>
          <b-col cols="12" class="mt-4 px-0">
            <h4 class="font-weight-bold">Old Password</h4>
            <b-form-input type="password" class="bg-transparent" @input="updateProfile(`full_name`, $event)" v-model="password" placeholder="Enter your name"></b-form-input>
          </b-col>
          <b-col cols="12" class="mt-4 px-0">
            <h4 class="font-weight-bold">New Password</h4>
            <b-form-input type="password" class="bg-transparent" @input="updateProfile(`full_name`, $event)" v-model="password" placeholder="Enter your name"></b-form-input>
          </b-col>
          <b-col cols="12" class="mt-4 px-0">
            <h4 class="font-weight-bold">Confirm Password</h4>
            <b-form-input type="password" class="bg-transparent" @input="updateProfile(`full_name`, $event)" v-model="password" placeholder="Enter your name"></b-form-input>
          </b-col>
          <!-- Social Media -->
          <!-- <b-col cols="12" class="mt-4 px-0 social-media">
            <h4 class="font-weight-bold">Social Media</h4>
            <div class="social-media-item d-flex align-items-center mb-4">
              <div class="img">
                <img width="25px" height="25px" src="@/assets/images/owner/facebook.svg" alt="facebook">
              </div>
              <b-form-input class="bg-transparent" @input="updateProfile(`full_name`, $event)" v-model="password" placeholder="Enter your Facebook account"></b-form-input>
            </div>
            <div class="social-media-item d-flex align-items-center mb-4">
              <div class="img">
                <img width="25px" height="25px" src="@/assets/images/owner/instagram.svg" alt="facebook">
              </div>
              <b-form-input class="bg-transparent" @input="updateProfile(`full_name`, $event)" v-model="password" placeholder="Enter your Instagram account"></b-form-input>
            </div>
            <div class="social-media-item d-flex align-items-center mb-4">
              <div class="img">
                <img width="25px" height="25px" src="@/assets/images/owner/whatsapp.svg" alt="facebook">
              </div>
              <b-form-input class="bg-transparent" @input="updateProfile(`full_name`, $event)" v-model="password" placeholder="Enter your Whatsapp number"></b-form-input>
            </div>
          </b-col> -->
        </b-col>
        <b-col cols="9" class="text-center mx-auto mt-4">
          <b-button variant="primary" class="w-75">Save Changes</b-button>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import AvatarSection from '@/components/profile/AvatarSection';
import { VueTelInput } from 'vue-tel-input'
import 'vue-tel-input/dist/vue-tel-input.css'

export default {
  props: {
    form: {
      type: Object,
      required: true,
    },
  },
  components: {
    AvatarSection,
    VueTelInput,
  },
  data(){
    return {
      options: [{text: 'Private Owner / Tenant', value: 'Private Owner / Tenant'}, {text: 'Broker', value: 'corporate'}],
      type: '',
      genderOptions: [
        { value: 'm', text: this.$t('form.genderOptions.male') },
        { value: 'f', text: this.$t('form.genderOptions.female') },
      ],
      gender: '',
      password: '',
    }
  },
  mounted() {
    if(this.form.type === 'corporate'){
      this.type = this.dir === 'ltr' ? 'Broker' : 'وسيط عقارى';
    }else{
      this.type = this.dir === 'ltr' ? 'Private Owner / Tenant' : 'مالك / مستأجر خاص';
    }

    if(this.dir === 'ltr'){
      this.options = [{text: 'Private Owner / Tenant', value: 'Private Owner / Tenant'}, {text: 'Broker', value: 'corporate'}]
    }else{
      this.options = [{text: 'مالك / مستأجر خاص', value: 'Private Owner / Tenant'}, {text: 'وسيط عقارى', value: 'corporate'}]
    }
  },
  computed:{
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
    }
  },
  methods: {
    setSelectedOption(option){
      if(this.type === option.text){
        return
      }else{
        this.type = option.text;
        this.updateProfile('type', option.value)
      }
    },
    setSelectedGender(option){
      if(this.gender === option.text){
        return
      }else{
        this.gender = option.text;
        this.updateProfile('gender', option.value)
      }
    },
    updateProfile(prop, value) {
      this.$emit('updateProfile', prop, value)
    },
  }
}
</script>

<style lang="scss">
.user {
  background-color:#FDF2F2 !important;
  .custom-select {
    background-color:#FDF2F2 !important;
  }
}
.w-90 {
  width: 90% !important;
}
.dropdown-toggle {
  padding-left: 10px !important;
  padding-right: 10px !important;
  background-color: transparent !important;
}

a.dropdown-item {
  padding-left: 10px !important;
  padding-right: 10px !important;
}

div.vue-tel-input input {
  background-color: transparent !important;
}

.social-media {
  .social-media-item {
    position: relative;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
    border-collapse: collapse;
    .img {
      border: 1px solid #ced4da;
      padding: 0.35rem 0.25rem;
      border-radius: 0.25rem 0 0 0.25rem;
      border-collapse: collapse;
    }
    input {
      border-radius: 0 0.25rem 0.25rem 0;
      height: 38.2px !important;
    }
  }
}
</style>