<template>
  <section :class="['d-flex align-items-center', {'flex-column': !inMob}, {'makeGap': inMob}, {'mb-4': inMob}, {'justify-content-center': inMob}]">
    <!-- avatar profile img -->
    <div :class="['preview', {'inMob': inMob}]">
      <img
        class="avatar w-100 h-100"
        :src="previewImg ? previewImg : 'https://rentup-storage.s3.eu-central-1.amazonaws.com/image_content/users/user/default-avatar.png'"
        alt="avatar img"
      />
    </div>
    <div class="spacer-10" v-if="!inMob"></div>
    <b-alert
      class="rounded-0 mb-5 text-center"
      show
      variant="danger"
      v-if="image_error_message"
    >
      {{ image_error_message }}
    </b-alert>
    <div class="img_reviews">
      <label class="btn btn-transparent px-3 d-flex align-items-center">
        <input
          type="file"
          accept="image/png, image/jpe, image/jpeg, image/jpg"
          class="d-none"
          @change="updateProfile(`avatar`, $event)"
        />
        <img class="mb-1 mx-1" width="20px" height="20px" src="@/assets/images/owner/edit_img.svg" alt="Edit Image">
        <span>{{ $t('common.change') }}</span>
      </label>
      <!-- <div class="spacer-20" v-if="!inMob"></div>
      <p class="mb-0">
        <StarRateBox :rate="rateCount" :reviews-count="reviewsCount" />
      </p> -->
    </div>
  </section>
</template>

<script>
import Separator from '@/components/shared/Separator'
import StarRateBox from '@/components/shared/StarRateBox.vue'
import { compressImage, generateServerImageUrl } from '@/helpers/images'
import { isValidImage } from '@/helpers/imageValidation'
export default {
  props: {
    avatar: {
      type: String,
      default: '',
      required: true,
    },
    user: {
      type: Object,
      default: () => {},
    },
    inMob:{
      type: Boolean,
      default: false
    }
  },
  components: {
    Separator,
    StarRateBox,
  },
  data() {
    return {
      previewImg: '',
      image_error_message: '',
    }
  },
  created() {
    if (typeof this.avatar === 'string' || this.avatar === '' || !this.avatar) {
      this.previewImg = generateServerImageUrl({
        url: this.avatar,
      })
    } else {
      this.previewImg = URL.createObjectURL(this.avatar)
    }
  },
  computed: {
    rateCount() {
      if (this.user.isLandlord) {
        return this.user.landlord_rating_avg
      } else {
        return this.user.tenant_rating_avg
      }
    },
    reviewsCount() {
      if (this.user.isLandlord) {
        return this.user.landlord_rating_count
      } else {
        return this.user.tenant_rating_count
      }
    },
  },
  methods: {
    updateProfile(prop, event) {
      const imageFile = event.target.files[0]
      if (!imageFile) return

      if (isValidImage(imageFile)) {
        compressImage({
          file: event.target.files[0],
          callback: (file) => {
            this.previewImg = URL.createObjectURL(file)

            this.$emit('updateProfile', prop, file)
          },
        })
        this.image_error_message = ''
      } else {
        this.image_error_message = this.$t('profile.imageErrorMessage')
      }
    },
  },
}
</script>

<style lang="scss" scoped>
.makeGap {
  gap: 20px;
}
.preview {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  overflow: hidden;
  border: solid 1px $grayLightColor;
  background-color: #ffffff;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  &.inMob {
    width: 100px;
    height: 100px;
  }
  .avatar {
    object-fit: contain;
  }
}
</style>
