              <template #title>
                <b-button variant="outline-primary">
                  Broker Settings
                </b-button>
              </template>
              <div class="content mt-4">
                <b-row>
                  <!-- User Access -->
                  <b-col cols="12" md="12" lg="6" class="mx-auto">
                    <b-row>
                      <b-col cols="12" sm="9" lg="12" class="mx-sm-auto mx-lg-0 mb-3">
                        <h2 class="font-weight-semi-bold text-body mb-4">User Access</h2>
                      </b-col>
                      <b-col cols="12" sm="9" lg="6" class="mx-sm-auto mx-lg-0 mb-3">
