                      </b-col>
                    </b-row>
                  </b-col>
                  <hr class="w-90 mx-auto my-4">
                  <!-- Company Information -->
                  <b-col cols="12">
                    <b-row>
                      <b-col cols="6" lg="3" offset="3" offset-lg="0">
                        <AvatarSection
                          class="d-flex"
                          :user="form"
                          :avatar="form.avatar"
                          @updateProfile="updateProfileHandler" />
                      </b-col>
                      <b-col cols="12" sm="9" lg="6" offset-lg="0" class="mx-sm-auto mx-lg-0">
                        <h2 class="font-weight-semi-bold text-body mb-4">Company Information</h2>
                        <b-row class="mb-3">
                          <b-col cols="12">
                            <h5 class="font-weight-bold">Company Name</h5>                        
                            <b-form-input class="bg-transparent" @input="updateProfile(`full_name`, $event)" v-model="form.email" placeholder="Enter your name"></b-form-input>
                          </b-col>
                        </b-row>
                        <b-row class="mb-3">
                          <b-col cols="12">
                            <h5 class="font-weight-bold">Company Offecial Name</h5>                        
                            <b-form-input class="bg-transparent" @input="updateProfile(`full_name`, $event)" v-model="form.email" placeholder="Enter your name"></b-form-input>
                          </b-col>
                        </b-row>
                        <b-row class="mb-3">
                          <b-col cols="12" xl="6" class="mb-3">
                            <h5 class="font-weight-bold">Commerical Register</h5>                        
                            <b-form-input class="bg-transparent mt-3" @input="updateProfile(`full_name`, $event)" v-model="form.email" placeholder="Enter your name"></b-form-input>
                          </b-col>
                          <b-col cols="12" xl="6" class="mb-3">
                            <h5 class="font-weight-bold">Upload Commerical Register Image</h5>                        
                            <label class="btn bg-transparent px-0 d-flex align-items-center pb-0" for="commercialImg">
                              <input
                                type="file"
                                accept="image/png, image/jpe, image/jpeg, image/jpg"
                                class="d-none"
                                @change="updateProfile(`avatar`, $event)"
                                id="commercialImg"
                              />
                              <div class="uploadImg d-flex rounded align-items-center w-100 justify-content-center">
                                <img class="mb-0 mx-1" width="20px" height="20px" src="@/assets/images/owner/upload.svg" alt="Edit  Image">
                                <span class="text-primary">Upload Commercial Registry Image</span>
                              </div>
                            </label>
                          </b-col>
                        </b-row>
                        <b-row class="mb-3">
                          <b-col cols="12">
                            <h5 class="font-weight-bold">Address</h5>                        
                            <b-form-input class="bg-transparent" @input="updateProfile(`full_name`, $event)" v-model="form.email" placeholder="Enter your name"></b-form-input>
                          </b-col>
                        </b-row>
                        <b-row class="mb-3">
                          <b-col cols="12">
                            <h5 class="font-weight-bold">Website</h5>                        
                            <b-form-input class="bg-transparent" @input="updateProfile(`full_name`, $event)" v-model="form.email" placeholder="Enter your name"></b-form-input>
                          </b-col>
                        </b-row>
                        <b-row class="mb-3">
                          <b-col cols="12">
                            <h5 class="font-weight-bold">Email address</h5>                        
                            <b-form-input class="bg-transparent" @input="updateProfile(`full_name`, $event)" v-model="form.email" placeholder="Enter your name"></b-form-input>
                          </b-col>
                        </b-row>
                      </b-col>
                    </b-row>
                  </b-col>

                  <hr class="w-90 mx-auto my-4">
                  <!-- Contact Person -->
                  <b-col cols="12" sm="9" lg="6" offset-lg="0" class="mx-sm-auto">
                    <b-row>
                      <b-col cols="12" class="mb-4">
                        <h5 class="font-weight-bold">Company Name</h5>  
                        <b-form-input class="bg-transparent" @input="updateProfile(`full_name`, $event)" v-model="form.email" placeholder="Enter your name"></b-form-input>
                      </b-col>
                      <b-col cols="12" class="mb-4">
                        <h5 class="font-weight-bold">Company Name</h5>  
                        <b-form-input class="bg-transparent" @input="updateProfile(`full_name`, $event)" v-model="form.email" placeholder="Enter your name"></b-form-input>
                      </b-col>
                      <b-col cols="12" class="mb-4">
                        <h5 class="font-weight-bold">Company Name</h5>  
                        <b-form-input class="bg-transparent" @input="updateProfile(`full_name`, $event)" v-model="form.email" placeholder="Enter your name"></b-form-input>
                      </b-col>
                      <b-col cols="6" offset="3" class="text-center">
                        <b-button variant="primary" class="w-75">Save Changes</b-button>
                      </b-col>
                    </b-row>
                  </b-col>
                </b-row>
              </div>
            </b-tab>
            <!-- Agents Data and CRUD Operations -->
            <b-tab :title-link-class="['bg-transparent', 'tabLink', 'p-2']">
              <template #title>
                <b-button variant="outline-primary" @click="isAdd_Edit = false">
                  Manage Agents
                </b-button>
              </template>
              <div class="agents_content mt-4">
                <b-row>

                  <!-- Agents List -->
                  <b-col cols="8" offset="2" v-if="!isAdd_Edit">
                    <div class="agent_header d-flex align-items-center justify-content-between">
                      <h2 class="font-weight-bold text-body">
                        My Agents
                      </h2>
                      <b-button size="sm" class="d-flex align-items-center text-primary bg-transparent" @click="isAdd_Edit = true">
                        <span class="p-1 font-size-large d-flex align-items-center justify-content-center">+</span>
                        <span class="font-weight-semi-bold">Add Agent</span>
                      </b-button>
                    </div>
                    <div class="agents_wrapper mt-4">
                      <b-row cols="12">
                        <b-col cols="12" md="6" xl="4" v-for="n in 12" :key="n">
                          <div class="agent d-flex align-items-center bg-white p-2 rounded">
                            <b-modal :id="`modal-${n}`" centered hide-footer hide-header>
                              <div class="actions">
                                <b-row>
                                  <b-col cols="12">
                                    <b-button block class="agentBtn mb-3">
                                      <img :class="[dir === 'ltr' ? 'mr-1' : 'ml-1']" width="20px" height="20px" src="@/assets/images/owner/NotePencil.svg" alt="Edit Agent">
                                      Edit Agent
                                    </b-button>
                                  </b-col>
                                  <b-col cols="12">
                                    <b-button block class="agentBtn">
                                      <img :class="[dir === 'ltr' ? 'mr-1' : 'ml-1']" width="20px" height="20px" src="@/assets/images/owner/Trash.svg" alt="Delete Agent">
                                      Delete Agent
                                    </b-button>
                                  </b-col>
                                </b-row>
                              </div>
                            </b-modal>
                            <img @click="$bvModal.show(`modal-${n}`)" width="20px" height="20px" src="@/assets/images/owner/agent_options.svg" alt="Agnet options">
                            <div class="img">
                              <img width="20px" height="20px" src="@/assets/images/owner/agent.svg" alt="Agnet Image">
                            </div>
                            <div class="content">
                              <h3 class="font-weight-bold">Salem Tarek</h3>
                              <h5>0201093967431</h5>
                            </div>
                          </div>
                        </b-col>
                      </b-row>
                    </div>
                  </b-col>

                  <!-- This is For Add and Edit Agent -->
                  <b-col cols="12" md="8" offset-md="2" v-else>
                    <b-row>
                      <b-col cols="8" lg="4" class="mx-auto">
                        <AvatarSection
                          class="d-flex"
                          :user="form"
                          :avatar="form.avatar"
                          @updateProfile="updateProfileHandler" />
                      </b-col>
                      <b-col cols="12" sm="9" lg="8" offset-lg="0" class="mx-sm-auto mx-lg-0 mt-4">
                        <h2 class="font-weight-semi-bold text-body mb-4">Company Information</h2>
                        <b-row class="mb-3">
                          <b-col cols="12">
                            <b-row>
                              <b-col xl="6" class="mb-3">
                                <h5 class="font-weight-bold">User Name</h5>                        
                                <b-form-input class="bg-transparent" @input="updateProfile(`full_name`, $event)" placeholder="Enter your name"></b-form-input>
                              </b-col>
                              <b-col xl="6" class="mb-3">
                                <h5 class="font-weight-bold">Password</h5>                        
                                <b-form-input type="password" class="bg-transparent" @input="updateProfile(`full_name`, $event)" placeholder="Enter your name"></b-form-input>
                              </b-col>
                            </b-row>
                          </b-col>
                        </b-row>
                        <b-row class="mb-3">
                          <b-col cols="12" class="mb-4">
                            <h5 class="font-weight-bold">Agent Mobile Number</h5>                        
                            <b-form-input class="bg-transparent" @input="updateProfile(`full_name`, $event)" v-model="form.email" placeholder="Enter your name"></b-form-input>
                          </b-col>
                          <b-col cols="12" class="mb-4">
                            <h5 class="font-weight-bold">Agent Email Address</h5>                        
                            <b-form-input class="bg-transparent" @input="updateProfile(`full_name`, $event)" v-model="form.email" placeholder="Enter your name"></b-form-input>
                          </b-col>
                          <b-col cols="12" class="mb-4">
                            <h5 class="font-weight-bold">Agent Gender</h5>                        
                            <b-dropdown :text="form.gender || genderOptions[0].text" class="my-2 outlined" menu-class="w-100" block>
                              <template #button-content>
                                <div class="dropdown-header d-flex align-items-center justify-content-between w-100 px-1">
                                  <h3 class="mb-0">{{ gender }}</h3>
                                  <img src="@/assets/images/icons/downCaret.svg" alt="Down Caret" width="20px" height="20px">
                                </div>
                              </template>
                              <b-dropdown-item @click="setSelectedGender(option)" v-for="(option, index) in genderOptions" :key="index">{{ option.text }}</b-dropdown-item>
                            </b-dropdown>
                          </b-col>
                          <b-col cols="12" lg="8" class="mx-auto text-center">
                            <b-button variant="primary" class="w-50">
                              Save Changes
                            </b-button>
                          </b-col>
                        </b-row>
                      </b-col>
                    </b-row>
                  </b-col>

                </b-row>
              </div>
            </b-tab>
          </b-tabs>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import AvatarSection from '@/components/profile/AvatarSection';
import { VueTelInput } from 'vue-tel-input'
import 'vue-tel-input/dist/vue-tel-input.css'

export default {
  props: {
    form: {
      type: Object,
      required: true,
    },
  },
  components: {
    AvatarSection,
    VueTelInput,
  },
  data(){
    return {
      options: [{text: 'Private Owner / Tenant', value: 'Private Owner / Tenant'}, {text: 'Broker', value: 'corporate'}],
      type: '',
      genderOptions: [
        { value: 'm', text: this.$t('form.genderOptions.male') },
        { value: 'f', text: this.$t('form.genderOptions.female') },
      ],
      gender: '',
      password: '',
      isAdd_Edit: false,
    }
  },
  mounted() {
    if(this.form.type === 'corporate'){
      this.type = this.dir === 'ltr' ? 'Broker' : 'وسيط عقارى';
    }else{
      this.type = this.dir === 'ltr' ? 'Private Owner / Tenant' : 'مالك / مستأجر خاص';
    }

    if(this.dir === 'ltr'){
      this.options = [{text: 'Private Owner / Tenant', value: 'Private Owner / Tenant'}, {text: 'Broker', value: 'corporate'}]
    }else{
      this.options = [{text: 'مالك / مستأجر خاص', value: 'Private Owner / Tenant'}, {text: 'وسيط عقارى', value: 'corporate'}]
    }
  },
  computed:{
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
    }
  },
  methods: {
    setSelectedOption(option){
      if(this.type === option.text){
        return
      }else{
        this.type = option.text;
        this.updateProfile('type', option.value)
      }
    },
    setSelectedGender(option){
      if(this.gender === option.text){
        return
      }else{
        this.gender = option.text;
        this.updateProfile('gender', option.value)
      }
    },
    updateProfile(prop, value) {
      this.$emit('updateProfile', prop, value)
    },
  }
}
</script>

<style lang="scss">

.actions {
  button.agentBtn {
    background-color: rgba(255, 82, 82, .2) !important;
    color: #db0909 !important;
    transition: .5s !important;
    &:hover {
      background-color: rgba(255, 82, 82, .5) !important;
      // background-color: rgba(220, 53, 69, .45) !important;
    }
  }
}
.agents_content {
  .agent_header {
    .btn {
      gap: 5px;
      border: 1px solid #004ed4;
      &:hover {
        background-color: #004ed4 !important;
        color: #FFF !important;
        span:first-of-type {
          border: 1px solid #fff;
        }  
      }
      span:first-of-type {
        width: 20px;
        height: 20px;
        border-radius: 50%;
        border: 1px solid #004ed4;
      }
    }
  }
  .agents_wrapper {
    .agent {
      gap: 10px;
      margin: 10px 0;
      & > img {
        position: absolute;
        right: 20px;
        top: 20px;
        cursor: pointer;
      }
      // max-width: 250px;
      .img {
        width: 60px;
        height: 60px;
        border-radius: 50%;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}
.w-90 {
  width: 90% !important;
}
.broker {
  background-color:#FDF2F2 !important;
  min-height: calc(100vh - 220px) !important;
  // padding-bottom: 120px !important;
  .content {
      // display: none;
      .uploadImg {
        border: 1px solid #004ed4 !important;
        padding: 7px !important;
        span {
          font-size: 13px !important;
        }
      }
    }
}
.noBorder {
  border: none !important;
  .tabLink {
    border: none;
    &.activeClassTab {
      button {
        background-color: #004ed4 !important;
        color: #fff !important;
      }
    }
  }
  .tabLink:hover, .tabLink:focus {
    isolation:none !important;
    border: none;
  }
}
</style>